.content {
    height: auto;
    margin: 50px auto;

    .category-wrapper {
        margin-bottom: 115px;

        .category {
            background-color: $base-background;
            text-align: center;
            padding: 5px 30px;
            position: relative;
            z-index: 1000;
            color: $base-color;
            top: -5px;
            font-size: 1.4em; }

        .category-separator {
            width: 100%;
            height: 13px;
            border-bottom: 1px solid $base-color;
            text-align: center; }

        .item {
            margin-top: 30px;
            max-height: 500px;

            .item-title {
                line-height: 1.5em;
                font-weight: 600;
                font-stretch: ultra-condensed; }

            .item-date {
                color: lighten($color, 35%);
                font-size: .8em;
                font-weight: 300;
                margin-bottom: 0; }

            .post-owner {
                max-height: 52px;

                .post-owner-figure {
                    max-width: 52px;
                    max-height: 52px;
                    object-fit: cover;
                    border-radius: 50%;
                    padding: 2px;
                    border: 1px solid lighten($base-color, 25%); }

                .owner-name, .item-date {
                    margin: 0; }

                .owner-name {
                    margin-top: 4px; }

                .owner-role {
                    color: lighten($color, 35%);
                    font-size: .8em;
                    font-weight: 300; } }


            .item-description {
                font-weight: 300; }

            &.item-block {
                .img-pinned {
                    width: 100%;
                    max-height: 400px;
                    height: auto;
                    object-fit: cover; } }


            &.item-inline {

                .post-img {
                    object-fit: cover;
                    min-height: 200px;
                    max-height: 200px;
                    min-width: 328px;
                    width: 100%;
                    margin-bottom: 20px; } }

            &.dual {
                .post-img {
                    min-height: 300px;
                    max-height: 300px; } }

            &.multiple {

                [class^='col-md-'] {
                    max-width: 260px;
                    margin: 30px auto; }

                .post-img {
                    min-width: 230px;
                    max-width: 230px; }

                .post-owner {
                    .col-sm-10 {
                        padding-left: 35px; } } } }

        .load-more {
            width: 100%;
            margin: 50px auto;
            text-align: center; } } }
